import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Project} from '../model/Project';
import {WordGroup} from '../model/Group';
import {ProjectService} from '../project.service';

@Component({
  selector: 'app-add-group',
  templateUrl: './add-group.component.html',
  styleUrls: ['./add-group.component.css']
})
export class AddGroupComponent implements OnInit {
  @Input() project: Project;
  @Input() title: string;
  @Output() end = new EventEmitter<WordGroup>();
  currentInput: string;
  editId = -1;

  @Input() group: WordGroup;
  constructor( private projectService: ProjectService ) { }
  ngOnInit() {
    if (!this.group) {
      this.group = {name: '', others: [], id: 0};
    }
  }

  add(): void {
    if (this.editId !== -1) {
      if (this.editId === -2) {
        this.group.name = this.currentInput;
      } else {
        this.group.others[this.editId] = this.currentInput;
      }
      this.editId = -1;
    } else if (this.currentInput) {
      if (this.group.name) {
        this.group.others.push(this.currentInput);
      } else {
        this.group.name = this.currentInput;
      }
    }
    this.currentInput = '';
  }
  edit(id: number, word: string): void {
    this.editId = id;
    this.currentInput = word;
  }
  clear(): void {
    if (this.group.id === 0) {
      this.editId = -1;
      this.currentInput = '';
      this.group = {name: '', others: [], id: 0};
    } else {
      this.projectService.removeGroup(this.group.id).subscribe( data => this.end.emit(null) );
    }
  }
  save(): void {
    if (this.group.id === 0) {
      this.projectService.addGroup(this.project.id, this.group).subscribe(data => {
        console.log(data);
        this.clear();
      });
    } else {
      this.projectService.updateGroup(this.group).subscribe(data => {
        console.log(data);
        this.end.emit(this.group);
      });
    }
  }
}



